<template>
  <div class="container" v-if="hasContent">
    <div>
      <div style="text-align: left; float: left">
        <span style="color: green; font-size: 18px; font-weight: bold"
          >查詢結果</span
        >
      </div>
      <div style="text-align: right">
        <span style="font-weight: bold">共 {{ total }} 筆資料</span>
      </div>
    </div>
    <br />
    <b-table
      :items="content.items"
      :fields="content.fields"
      head-variant="primary"
      hover
      style="text-align: center"
    >
    </b-table>
  </div>
  <div v-if="!hasContent"></div>
</template>

<script>
import { reactive, onMounted, ref } from "vue";
import { BTable } from "bootstrap-vue-next";
import { useRoute } from "vue-router";

export default {
  name: "single-query",
  props: {},
  components: {
    BTable,
  },
  setup() {
    const route = useRoute();

    const hasContent = ref(false);

    //init '' or 0
    const total = ref(0);

    const content = reactive({
      fields: [
        { key: "originalAddress", label: "原地址" },
        { key: "fullAddress", label: "標準地址" },
        { key: "addressId", label: "地址編碼" },
        { key: "xy", label: "經緯度" },
        { key: "joinStep", label: "比對代碼" },
      ],
      items: [],
    });

    onMounted(async () => {
      await findJson();
    });

    const keys = ["os", "full", "id", "xy", "js"];
    let joinStepsMap = [
      { key: "JA1", text: "最嚴謹比對" },
      { key: "JA2", text: "排除鄰比對" },
      { key: "JA3", text: "排除村、里比對" },
      { key: "JB1", text: "退室比對" },
      { key: "JB2", text: "樓之之樓比對" },
      { key: "JB3", text: "退樓後之比對" },
      { key: "JB4", text: "退樓比對" },
      { key: "JB5", text: "號之之號比對" },
      { key: "JC1", text: "臨建特附進階比對" },
      { key: "JC2", text: "路地名進階比對" },
      { key: "JC3", text: "缺路地名進階比對" },
      { key: "JC4", text: "號樓之要件進階比對" },
      { key: "JD1", text: "排除要件對應多址" },
      { key: "JD2", text: "地址明確拆分多址" },
      { key: "JD3", text: "退室多址比對" },
      { key: "JD4", text: "退樓後之多址比對" },
      { key: "JD5", text: "退樓多址比對" },
      { key: "JD6", text: "附近地址比對" },
      { key: "JD7", text: "增編多址比對" },
      { key: "JE1", text: "公設" },
      { key: "JE2", text: "具方向性地址" },
      { key: "JE3", text: "特殊地址" },
      { key: "JE4", text: "地址要件不齊" },
      { key: "JE5", text: "地址不存在母體" },
      { key: "JE61", text: "門牌廢止" },
      { key: "JE62", text: "異動軌跡有異" },
      { key: "JE9", text: "其他" },
    ];

    function isNullorEmpty(str) {
      if (str == null || str == "") {
        return true;
      }
    }

    function mappingJoinStep(joinStepArray) {
      console.log("joinStepArray:", joinStepArray);
      if (!Array.isArray(joinStepArray)) {
        return [];
      }

      let result = [];
      joinStepArray.forEach((js) => {
        joinStepsMap.forEach((x) => {
          if (`${js}`.substring(0, 3) == `${x.key}`) {
            console.log("js:, x.key:.x.text:", js, x.key, x.text);
            result.push(`${js}:${x.text}`);
          }
        });
      });
      // console.log("joinStepArray:", joinStepArray);
      // console.log("result:", result);
      joinStepArray.splice(0, joinStepArray.length, ...result);
      return result;
    }

    function isVaildy() {
      let keysArray = Object.keys(route.query);
      keysArray = keysArray.filter((key) => !keys.includes(key));
      // console.log("keysArray:", keysArray);
      if (keysArray.length > 0) {
        return false;
      }
      return true;
    }

    function findJson() {
      if (!isNullorEmpty(route)) {
        let keysArray = Object.keys(route.query);
        if (keysArray.length != keys.length) {
          return;
        }

        let map = isVaildy();
        if (!map) {
          return;
        }

        let object = {};
        let originalAddressStr = "";
        keys.forEach((key) => {
          if (!isNullorEmpty(route.query[key])) {
            if (key == "os") {
              originalAddressStr = route.query[key];
              // console.log("originalAddress:", originalAddressStr);
            }
            let temp;
            temp = route.query[key].split(",").filter((str) => {
              return !isNullorEmpty(str);
            });

            Object.defineProperty(object, key, {
              value: temp,
            });
          }
        });

        // console.log("object:", object);

        mappingJoinStep(object.js);

        setContentItems(object, originalAddressStr);

        total.value = content.items.length;
        hasContent.value = true;
        // console.log("hasContent.value:", hasContent.value);
      }
    }

    function setContentItems(object, originalAddressStr) {
      let item = {};
      for (let len = 0; len < object.full.length; len++) {
        item = {
          originalAddress: originalAddressStr,
          fullAddress: object.full[len],
          addressId: object.id[len],
          xy: `${object.xy[len]}`.replace(":", ","),
          joinStep: object.js[len],
        };
        content.items.push(item);
      }
    }

    return {
      hasContent,
      total,
      content,
    };
  },
};
</script>

<style></style>
